@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300;400;500;600;700&display=swap');

@layer base {
  html {
    @apply scroll-smooth;
  }
  
  body {
    @apply font-sans bg-gradient-to-br from-navy-50 via-white to-primary-50 text-navy-900 antialiased;
    background-attachment: fixed;
  }
  
  * {
    @apply box-border;
  }

  /* Scrollbar personalizada */
  ::-webkit-scrollbar {
    @apply w-2;
  }

  ::-webkit-scrollbar-track {
    @apply bg-navy-100;
  }

  ::-webkit-scrollbar-thumb {
    @apply bg-primary-400 rounded-full;
  }

  ::-webkit-scrollbar-thumb:hover {
    @apply bg-primary-500;
  }
}

@layer components {
  /* Botões Modernos */
  .btn {
    @apply inline-flex items-center justify-center px-6 py-3 rounded-xl font-semibold text-sm transition-all duration-300 focus:outline-none focus:ring-4 focus:ring-opacity-50 transform hover:scale-105 active:scale-95 shadow-soft;
  }
  
  .btn-primary {
    @apply bg-gradient-to-r from-primary-600 to-primary-700 hover:from-primary-700 hover:to-primary-800 text-white focus:ring-primary-300 shadow-glow;
  }
  
  .btn-secondary {
    @apply bg-gradient-to-r from-navy-600 to-navy-700 hover:from-navy-700 hover:to-navy-800 text-white focus:ring-navy-300;
  }
  
  .btn-success {
    @apply bg-gradient-to-r from-success-600 to-success-700 hover:from-success-700 hover:to-success-800 text-white focus:ring-success-300;
  }
  
  .btn-warning {
    @apply bg-gradient-to-r from-warning-600 to-warning-700 hover:from-warning-700 hover:to-warning-800 text-white focus:ring-warning-300;
  }
  
  .btn-danger {
    @apply bg-gradient-to-r from-danger-600 to-danger-700 hover:from-danger-700 hover:to-danger-800 text-white focus:ring-danger-300;
  }

  .btn-gold {
    @apply bg-gradient-to-r from-gold-500 to-gold-600 hover:from-gold-600 hover:to-gold-700 text-white focus:ring-gold-300 shadow-glow-gold;
  }

  .btn-outline {
    @apply bg-transparent border-2 border-primary-600 text-primary-600 hover:bg-primary-600 hover:text-white focus:ring-primary-300;
  }

  .btn-ghost {
    @apply bg-transparent text-navy-600 hover:bg-navy-100 focus:ring-navy-300 shadow-none;
  }

  .btn-sm {
    @apply px-4 py-2 text-xs;
  }

  .btn-lg {
    @apply px-8 py-4 text-base;
  }

  /* Cards Elegantes */
  .card {
    @apply bg-white/80 backdrop-blur-sm rounded-2xl shadow-soft border border-white/20 p-6 transition-all duration-300 hover:shadow-medium hover:bg-white/90;
  }
  
  .card-hover {
    @apply hover:shadow-strong hover:border-primary-200 hover:-translate-y-1 cursor-pointer;
  }

  .card-premium {
    @apply bg-gradient-to-br from-gold-50 to-gold-100 border-gold-200 shadow-glow-gold;
  }

  .card-dark {
    @apply bg-navy-900/90 text-white border-navy-700;
  }

  /* Inputs Modernos */
  .input {
    @apply w-full px-4 py-3 border-2 border-navy-200 rounded-xl bg-white/80 backdrop-blur-sm focus:outline-none focus:ring-4 focus:ring-primary-300 focus:border-primary-500 transition-all duration-300 placeholder-navy-400;
  }

  .input-error {
    @apply border-danger-500 focus:ring-danger-300 focus:border-danger-500;
  }

  .input-success {
    @apply border-success-500 focus:ring-success-300 focus:border-success-500;
  }

  /* Labels */
  .label {
    @apply block text-sm font-semibold text-navy-700 mb-2;
  }

  /* Badges Elegantes */
  .badge {
    @apply inline-flex items-center px-3 py-1 rounded-full text-xs font-semibold transition-all duration-200;
  }
  
  .badge-primary {
    @apply bg-primary-100 text-primary-800 border border-primary-200;
  }
  
  .badge-success {
    @apply bg-success-100 text-success-800 border border-success-200;
  }
  
  .badge-warning {
    @apply bg-warning-100 text-warning-800 border border-warning-200;
  }
  
  .badge-danger {
    @apply bg-danger-100 text-danger-800 border border-danger-200;
  }

  .badge-gold {
    @apply bg-gold-100 text-gold-800 border border-gold-200;
  }

  .badge-navy {
    @apply bg-navy-100 text-navy-800 border border-navy-200;
  }

  /* Títulos Elegantes */
  .title-primary {
    @apply text-3xl md:text-4xl font-bold bg-gradient-to-r from-primary-600 to-navy-700 bg-clip-text text-transparent;
  }

  .title-secondary {
    @apply text-2xl md:text-3xl font-semibold text-navy-800;
  }

  .title-gold {
    @apply text-2xl md:text-3xl font-bold bg-gradient-to-r from-gold-600 to-gold-700 bg-clip-text text-transparent;
  }

  /* Containers */
  .container-main {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
  }

  .container-narrow {
    @apply max-w-4xl mx-auto px-4 sm:px-6 lg:px-8;
  }

  /* Navegação */
  .nav-link {
    @apply flex items-center px-4 py-3 text-navy-600 hover:text-primary-600 hover:bg-primary-50 rounded-xl transition-all duration-200 font-medium;
  }

  .nav-link-active {
    @apply text-primary-600 bg-primary-100 border-r-4 border-primary-600;
  }

  /* Modais e Overlays */
  .modal-overlay {
    @apply fixed inset-0 bg-navy-900/50 backdrop-blur-sm z-50 flex items-center justify-center p-4;
  }

  .modal-content {
    @apply bg-white rounded-2xl shadow-strong max-w-md w-full p-6 animate-scale-in;
  }

  /* Loading States */
  .loading-spinner {
    @apply animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600;
  }

  .loading-dots {
    @apply flex space-x-1;
  }

  .loading-dot {
    @apply h-2 w-2 bg-primary-600 rounded-full animate-bounce;
  }

  /* Gradientes de Fundo */
  .bg-legal-gradient {
    @apply bg-gradient-to-br from-navy-900 via-primary-900 to-navy-800;
  }

  .bg-hero-gradient {
    @apply bg-gradient-to-r from-primary-600 via-primary-700 to-navy-700;
  }

  .bg-card-gradient {
    @apply bg-gradient-to-br from-white to-primary-50;
  }

  /* Efeitos de Hover */
  .hover-lift {
    @apply transition-transform duration-300 hover:-translate-y-2;
  }

  .hover-glow {
    @apply transition-all duration-300 hover:shadow-glow;
  }

  .hover-scale {
    @apply transition-transform duration-300 hover:scale-105;
  }

  /* Estados de Foco */
  .focus-ring {
    @apply focus:outline-none focus:ring-4 focus:ring-primary-300 focus:ring-opacity-50;
  }

  /* Divisores */
  .divider {
    @apply border-t border-navy-200 my-6;
  }

  .divider-vertical {
    @apply border-l border-navy-200 mx-6;
  }

  /* Texto */
  .text-muted {
    @apply text-navy-500;
  }

  .text-emphasis {
    @apply text-navy-800 font-semibold;
  }

  /* Alertas */
  .alert {
    @apply p-4 rounded-xl border-l-4 mb-4;
  }

  .alert-info {
    @apply bg-primary-50 border-primary-500 text-primary-800;
  }

  .alert-success {
    @apply bg-success-50 border-success-500 text-success-800;
  }

  .alert-warning {
    @apply bg-warning-50 border-warning-500 text-warning-800;
  }

  .alert-danger {
    @apply bg-danger-50 border-danger-500 text-danger-800;
  }

  /* Tabelas */
  .table {
    @apply w-full border-collapse bg-white rounded-xl overflow-hidden shadow-soft;
  }

  .table th {
    @apply bg-navy-50 px-6 py-4 text-left text-xs font-semibold text-navy-700 uppercase tracking-wider border-b border-navy-200;
  }

  .table td {
    @apply px-6 py-4 whitespace-nowrap text-sm text-navy-900 border-b border-navy-100;
  }

  .table tr:hover {
    @apply bg-navy-50;
  }
}

/* Animações Personalizadas */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes shimmer {
  0% {
    background-position: -200px 0;
  }
  100% {
    background-position: calc(200px + 100%) 0;
  }
}

.animate-fadeInUp {
  animation: fadeInUp 0.6s ease-out;
}

.animate-slideInRight {
  animation: slideInRight 0.4s ease-out;
}

.animate-slideInLeft {
  animation: slideInLeft 0.4s ease-out;
}

.animate-scaleIn {
  animation: scaleIn 0.3s ease-out;
}

.animate-shimmer {
  animation: shimmer 2s infinite linear;
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200px 100%;
}

/* Responsividade Avançada */
@media (max-width: 640px) {
  .btn {
    @apply px-4 py-2 text-sm;
  }
  
  .card {
    @apply p-4;
  }
  
  .title-primary {
    @apply text-2xl;
  }
  
  .title-secondary {
    @apply text-xl;
  }
}

/* Dark Mode Support */
@media (prefers-color-scheme: dark) {
  .dark-mode {
    @apply bg-navy-900 text-white;
  }
  
  .dark-mode .card {
    @apply bg-navy-800/80 border-navy-700;
  }
  
  .dark-mode .input {
    @apply bg-navy-800 border-navy-600 text-white;
  }
}

/* Print Styles */
@media print {
  .no-print {
    @apply hidden;
  }
  
  .print-only {
    @apply block;
  }
}

/* Acessibilidade */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus Visible para melhor acessibilidade */
.focus-visible:focus {
  @apply outline-none ring-4 ring-primary-300 ring-opacity-50;
}
